.notice-container{
    width: 250px;
    height: 400px;
    box-sizing: border-box;
    padding: 10px;
    .notice-title{
        font-weight: bold;
    
    }
    .notice-item{
        display: flex;
        justify-content: space-between;
    }
    .notice-txt{
        display: flex;
        justify-content:space-between;
        align-items: center;
        span {
            width: 240px;
            display: inline-block;
        }
    }
}
