#left-container{
    // min-width: 10px;
    position: relative;
    
    
}
#left-box{
    width: 250px;
    box-sizing: border-box;
    border-left: 1px solid #eee;
    padding-left: 10px;
    // transition: display 500ms ease-in;
}
#hide{
    position: absolute;
    top: 300px;
    left: -10px;
    font-weight: bold;
    padding: 5px;
    text-align: center;
    border: 1px solid #999;
    border-radius: 20%;
    cursor: pointer;
    background-color: #fff;
}

/*入场动画开始*/
.fade-enter {
// width: 0;
    transform: translateX(100%);
}

/*入场动画过程*/
.fade-enter-active {
// opacity: 1;
    transform: none;
    transition: transform .3s ease-in;
}

/*入场动画结束*/
.fade-enter-done {
    transform: none;
}

/*离场动画开始*/
.fade-exit {
    transform: none;
}

/*离场动画过程*/
.fade-exit-active {
// width: 0;
    transform: translateX(100%);
    transition: transform .3s ease-in;
}

/*离场动画结束*/
.fade-exit-done {
    display: none;
}

/*页面第一次加载时的开始状态*/
.fade-appear {
    display: none;
}

/*页面第一次加载时的动画过程*/
.fade-appear-active {
    // width: 0;
    transform: translateX(100%);
    transition: transform .3s ease-in;
}