
$itemWidth : 14.25%;

.plan-container{
    flex: 1;
    padding: 10px;
    .plan-select-box{
        .plan-select-year{
            font-size: 20px;
        }
        .plan-select-month{
            font-size: 40px;
        }
    }
    .date-week-box,.date-context{
        // width: 840px;
        width: 100%;
        min-width: 840px;
        display: flex;
        flex-wrap: wrap;
    }
    .date-week-box{
        border-bottom: 1px solid #eee;
        .item{
            width: $itemWidth;
            height: 30px;
            line-height: 30px;
            text-align: center;
            font-weight: bold;
        }
    }
    .date-context{
        .item{
            width: $itemWidth;
            height: 140px;
            text-align: left;
            box-sizing: border-box;
            padding: 5px;
            box-shadow: 4px 4px 10px #eee;
            position: relative;
            display: flex;
            flex-wrap: wrap;
            &:nth-child(7n) {
                color: red;
            }
            &:nth-child(7n+1) {
                color: red;
            }
            +.un-item{
                color: #c1c1c1;
            }
            .item-top{
                width: 100%;
                height: calc(100% - 20%);
                display: flex;
                justify-content: space-between;
                span{
                    width: 20px;
                    text-align: center;
                }
            }
            .item-bottom{
                width: 100%;
                height: 20px;
                text-align: right;
            }
            textarea{
    
                height: 100%;
                border: none;
                padding: 2px;
                flex: 1;
                box-sizing: border-box;
                resize:none;
                &:focus{
                    outline:none;
                }
            }
        }
        
        .current-date{
            border: 2px solid rgb(31, 29, 29);
            border-radius: 4px;;
            font-weight: bold;
            box-sizing: border-box;
        }
    }
    textarea::-webkit-scrollbar {/*滚动条整体样式*/

        width: 6px;     /*高宽分别对应横竖滚动条的尺寸*/

        height: 1px;

    }

    textarea::-webkit-scrollbar-thumb {/*滚动条里面小方块*/

            border-radius: 3px;

            -webkit-box-shadow: inset 0 0 2px rgba(0,0,0,0.2);

            background: #535353;

        }

        textarea::-webkit-scrollbar-track {/*滚动条里面轨道*/

            // -webkit-box-shadow: inset 0 0 5px rgba(0,0,0,0.2);

            // border-radius: 10px;

            // background: #EDEDED;
            -webkit-box-shadow: transparent;

    }
}